*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: cursive;
}

header{
    display: flex;
    width: calc(100%/2);
    margin: 20px auto;
    padding: 1rem ;
    align-items: center;
    justify-content: center;
    background-color: antiquewhite;
    border-radius: 5px;
    border-style: solid;
    border-color: wheat;
}
header div:nth-child(1){
    margin-right: 10px;
}
table{
    margin: 20px auto;
    width: calc(100%/2);
}
table tr:nth-child(odd){
    animation: tableback 3000ms forwards;
}
td{
    text-align: center;
}
@keyframes tableback {
    0%{
        background-color: white;
    }
    100%{
    background-color: antiquewhite;
    }   
}
